function main() {
  const hamburgerButton: HTMLButtonElement = (() => {
    const elem = document.getElementById("hamburger")
    if (elem instanceof HTMLButtonElement) {
      return elem
    }

    throw new Error("Expected there to be a button#hamburger tag")
  })()

  const hamburgerContainer: HTMLDivElement = (() => {
    const elem = document.getElementById("hamburger-container")
    if (elem instanceof HTMLDivElement) {
      return elem
    }

    throw new Error("Expected there to be a div#hamburger-container tag")
  })()

  const mobileContainer: HTMLDivElement = (() => {
    const elem = document.getElementById("mobile-container")
    if (elem instanceof HTMLDivElement) {
      return elem
    }

    throw new Error("Expected there to be a div#mobile-container tag")
  })()

  const navOverlay: HTMLDivElement = (() => {
    const elem = document.getElementById("nav-overlay")
    if (elem instanceof HTMLDivElement) {
      return elem
    }

    throw new Error("Expected there to be a div#nav-overlay tag")
  })()

  const languageSelects = document.querySelectorAll<HTMLSelectElement>(
    "#language-desktop, #language-mobile"
  )

  const closeMenu = () => {
    hamburgerContainer.classList.remove("show")
    mobileContainer.classList.remove("show")
    hamburgerButton.classList.remove("show")
    navOverlay.classList.remove("show")
  }

  hamburgerButton.addEventListener("click", () => {
    toggleClass("show", hamburgerContainer)
    toggleClass("show", mobileContainer)
    toggleClass("show", hamburgerButton)
    toggleClass("show", navOverlay)
  })

  hamburgerContainer.addEventListener("click", (e) => {
    const target = e.target as HTMLElement
    const link = target.closest("a")
    if (link) closeMenu()
  })

  mobileContainer.addEventListener("click", (e) => {
    const target = e.target as HTMLElement
    const link = target.closest("a")
    if (link) closeMenu()
  })

  languageSelects.forEach(select => {
    select.addEventListener("change", (e) => {
      const value = (e.target as HTMLSelectElement).value
      window.location.href = value === "svenska" ? "/" : "/en"
    })
  })
}

function toggleClass(className: string, element: HTMLElement) {
  if ([...element.classList.values()].includes(className)) {
    element.classList.remove(className)
  } else {
    element.classList.add(className)
  }
}

if (document.readyState === "complete") {
  main()
} else {
  document.addEventListener("DOMContentLoaded", main)
}
